import React from 'react'
import { Link } from 'react-router-dom'

export default function Header() {
    return (
        <header style={headerStyle}>
            <h1>VTP Cloud</h1>
            <Link style={linkStyle} to="/"> Home </Link> |
            <Link style={linkStyle} to="/customer-support"> Customer Support </Link> |
            <Link style={linkStyle} to="/particle"> Particle Debug </Link> |
            <Link style={linkStyle} to="/task"> Task </Link> |
            <Link style={linkStyle} to="/about"> About </Link>
        </header>
    )
}

const linkStyle = {
    color: '#fff',
    textDecoration: 'none'
}

const headerStyle = {
    background: '#333',
    color: '#fff',
    textAlign: 'center',
    padding: '10px'
}
