import React, { Component } from 'react'
import axios from 'axios';

export class Particle extends Component {

    state = {
        posts: [],
        value: []
    }

    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    // componentDidMount(){
    //     axios.get('https://api.particle.io/v1/devices/42002f001751353338363036/rssi?access_token=a65d7d457a1e2a8c57cade66d549f827785df4bd')
    //         .then(res => {
    //             console.log(res)
    //             this.setState({
    //                 posts: res.data  
    //             })
    //         })
    // }

    // 'https://api.particle.io/v1/devices/42002f001751353338363036/rssi?access_token=a65d7d457a1e2a8c57cade66d549f827785df4bd'
    
    handleClick(e) {
        console.log('Click happened');
        console.log('Particle ID: ' + this.state.value);

        var apiPre = "https://api.particle.io/v1/devices/";
        var apiPost = "/rssi?access_token=a65d7d457a1e2a8c57cade66d549f827785df4bd";

        var apiFinal = apiPre + this.state.value + apiPost;
        console.log('Final API: ' + apiFinal);

        axios.get(apiFinal)
        .then(res => {
            console.log(res)
            this.setState({
                posts: res.data  
            })
        })
    }

    handleChange(event) {
        this.setState({value: event.target.value});
    }


    render() {
        const { posts } = this.state;
        return (
            <div className="container">
                <h1>Particle Debug</h1>

                <form>
                    <label>
                        Particle ID : 
                        <input style={inputStyle} type="text" value={this.state.value} onChange={this.handleChange} />
                    </label>
                    
                </form>

                <p>
                    {"Particle ID: " + this.state.value}
                </p>

                <button style={btnStyle} onClick={this.handleClick}>Find RSSI</button>

                <p>
                    {"RSSI = " + posts.result}
                </p>

            </div>
        )
    }
}

const btnStyle = {
    background: '#D5F2F2',
    color: '#000303',
    textAlign: 'center',
    padding: '10px'
}

const inputStyle = {
    width: '200px',
    height: '30px',
    background: '#D5F2F2'
}

export default Particle
