import React, { Component } from 'react';
import { BrowserRouter as Router, Route} from 'react-router-dom';
import Header from './components/layout/Header'
import Todos from './components/Todos';
import AddTodo from './components/AddTodo';
import About from './components/pages/About';
import Particle from './components/pages/Particle';
import CustomerSupport from './components/pages/CustomerSupport';

// import uuid from 'uuid';
import axios from 'axios';

import './App.css';
import Home from './components/pages/sensor-refurb/Home';

class App extends Component {
  state = {
    todos: []
  }

  // Request test JSON objects using GET request on load
  componentDidMount() {
    // axios.get('https://jsonplaceholder.typicode.com/todos?_limit=10')
    // .then(res => this.setState({ todos: res.data }))
  }

  // Toggle Complete
  markComplete = (id) => {
    console.log(id)
    this.setState({todos: this.state.todos.map(todo => {
      if(todo.id === id) {
        todo.completed = !todo.completed
      }
      return todo;
    }) })
  }

  // Delete Todo
  delTodo = (id) => {
    // axios.delete(`https://jsonplaceholder.typicode.com/todos`)
    this.setState({ todos: [...this.state.todos.filter(todo =>todo.id !== id)]})
  }

  // Add Todo
  addTodo = (title) => {
    axios.post('https://jsonplaceholder.typicode.com/todos', {
      title,
      completed: false
    })
    .then(res => this.setState({todos: [...this.state.todos, res.data]}));
  }

  render() {
    return (
      <Router>
        <div className="App">
          <div className="container">
            <Header />
            <Route exact path="/task" render={props => (
              <React.Fragment>
                <h1>Task List</h1>
                <AddTodo addTodo={this.addTodo}/>
                <Todos todos={this.state.todos} markComplete={this.markComplete} delTodo={this.delTodo} />
              </React.Fragment>
            )} />
            <Route exact path="/" component={Home} />
            <Route path="/about" component={About} />
            <Route path="/particle" component={Particle} />
            <Route path="/customer-support" component={CustomerSupport} />
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
