import React, { Component } from 'react'
import { Tab, TabPanel, Tabs, TabList } from "react-web-tabs";
import "react-tabs/style/react-tabs.css";
import "react-web-tabs/dist/react-web-tabs.css";
// import SensorRefurb from './sensor-refurb/SensorRefurb';
import AddCustomer from './customer/AddCustomer';
import RestApi from './rest-api-vs/RestApi';
import SensorRefurb from './sensor-refurb/SensorRefurb';

export class CustomerSupport extends Component {
    render() {
        return (
            <div>
                {/* <h1 style={headerStyle} >Verisolutions CRM</h1> */}

                <Tabs defaultTab="vertical-tab-three" vertical className="vertical-tabs">
                    <TabList style={tabStyle} >
                        <Tab tabFor="vertical-tab-one">Customer</Tab>
                        <Tab tabFor="vertical-tab-two">Sensor</Tab>
                        <Tab tabFor="vertical-tab-three">Refurbishment</Tab>
                        <Tab tabFor="vertical-tab-four">REST API - VS</Tab>
                    </TabList>
                
                    <TabPanel tabId="vertical-tab-one">
                        <h1>Customer History</h1>
                        <AddCustomer />
                    </TabPanel>

                    <TabPanel tabId="vertical-tab-two">
                        <h1>Sensor History</h1>
                        <h2>TBD: Add Tabs and Form to Add/Remove/Modify Sensor Details</h2>
                    </TabPanel>

                    <TabPanel tabId="vertical-tab-three">
                        <h1 style={headerStyle} >Sensor Refurbishment</h1>
                        <SensorRefurb />
                    </TabPanel>

                    <TabPanel tabId="vertical-tab-four">
                        <h1>REST API - VS Client</h1>
                        <RestApi />
                    </TabPanel>

                </Tabs>
            </div>
        )
    }
}

const tabStyle = {
    background: '#b4eaf1',
    color: '#fff',
    textAlign: 'center'
}
const headerStyle = {
    background: '#b4eaf1',
    textAlign: 'center'
}

export default CustomerSupport
