import React, { Component } from 'react';
// import axios from 'axios';
import { Tab, TabPanel, Tabs, TabList } from "react-web-tabs";
import "react-tabs/style/react-tabs.css";
import "react-web-tabs/dist/react-web-tabs.css";
import AddNewRecord from './AddNewRecord';
import UpdateRecord from './UpdateRecord';

export class SensorRefurb extends Component {

    render() {
        return (
            <div>
                <Tabs defaultTab="horizontal-tab-one" horizontal="true" className="horizontal-tabs">
                    <TabList style={tabStyle} >
                        <Tab tabFor="horizontal-tab-one">Add Record</Tab>
                        <Tab tabFor="horizontal-tab-two">Search Record</Tab>
                        <Tab tabFor="horizontal-tab-three">Modify Record</Tab>
                    </TabList>

                    <TabPanel tabId="horizontal-tab-one">
                        <h1>Add New Record</h1>
                        <AddNewRecord />
                    </TabPanel>

                    <TabPanel tabId="horizontal-tab-two">
                        <h1>Search Record</h1>
                    </TabPanel>

                    <TabPanel tabId="horizontal-tab-three">
                        <h1>Update Record</h1>
                        <UpdateRecord />
                    </TabPanel>

                </Tabs>

            </div>
        )
    }
}

const tabStyle = {
    background: '#b4eaf1',
    color: '#fff',
    textAlign: 'center'
}

export default SensorRefurb
